.setlists__title {
  font-size: 295%;
  margin: 56px 0px 0px 68px;
}

.setlists__panel-left > .setlists__title {
  margin-left: 365px;
}

.setlists__setlists-list, .setlists__jokes-list, .setlists__list-item {
  border-radius: 10px;
}

.setlists__search {
  border: none;
  outline: none;
}

.setlists__search:focus {
  border: none;
}

.setlists__setlists-list {
  margin: 11px 87px 0px 346px;
  height: 598px;
}

.setlists__jokes-list {
  margin: 6px 322px 0px 68px;
  height: 613px;
}

.setlists__list-item {
  font-size: 184%;
  padding: 11px;
}

.setlists__list-item--active, .setlists__list-item:hover {
  background-color: var(--bg-color-3);
}

.setlists__button {
  cursor: pointer;
  margin: 19px 322px 0px 68px;
  font-size: 209%;
  border-radius: 10px;
  padding: 8px;
  background-color: var(--bg-color-2);
}

.setlists__button:hover {
  background-color: var(--bg-color-3);
}
