.menu {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  background-color: var(--text-color);
  opacity: 0.6;
  padding-left: 100px;
}

.menu__burger-btn {
  margin: 25px;
  background-image: var(--burger-btn-img);
  width: 56px;
  height: 41px;
  background-size: 100% 100%;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 2;
}

.menu__burger-btn--open {
  background-image: var(--burger-close-btn-img);
}

.menu__item {
  font-size: 200%;
  padding: 30px;
  font-weight: bold;
  color: var(--bg-color);
}
