.recording {
  position: relative;
}

.recording__time {
  font-size: 170px;
  line-height: 232px;
  text-align: center;
  margin: 0 112px;
  color: #7879F1;
}

.recording__joke {
  font-size: 70px;
  line-height: 95px;
  text-align: center;
  
  color: #000000;
}

.recording__joke--active {
  font-size: 90px;
  line-height: 123px;
  text-align: center;
}

.recording__joke--active > * {
  color: #7879F1;
  font-weight: bold;
}

.has-stroke {
  -webkit-text-stroke-color: #000000;
  -webkit-text-stroke-width: 1px;
}

.recording__start-top-button {
  position: absolute;
  width: 297.63px;
  height: 54px;
  left: 1133px;
  top: 25px;

  background: #C4C4C4;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-size: 30px;
  line-height: 41px;
  color: #000000;
}

.recording__stop-icon, .recording__record-icon {
  width: 26px;
  height: 24px;
  left: 1150px;
  top: 43px;
  
  background: #FF0000;
}

.recording__record-icon {
  border-radius: 50%;
}

.recording__prompts {
  font-size: 70%;
  height: 60px;
  line-height: 100%;
}

.recording__prompt {
  margin: auto;
  color: #ffffff;
  font-weight: bold;
  -webkit-text-stroke-color: #000000;
  -webkit-text-stroke-width: 2px;
}
