.toggler {
  cursor: pointer;
  width: 72px;
  height: 38px;
  background-color: var(--button-bg-color);
  display: flex;
  align-items: stretch;
  border-radius: 30px;
}

.toggler--checked {
  justify-content: right;
}

.toggler__thumb {
  width: 30px;
  background-color: var(--button-text-color);
  border-radius: 50%;
  margin: 5px;
}