@font-face {
  font-family: Open Sans;
  src: url(../public/OpenSans/OpenSans-Regular.ttf);
}

@font-face {
  font-family: Open Sans;
  font-weight: lighter;
  src: url(../public/OpenSans/OpenSans-Light.ttf);
}

@font-face {
  font-family: Open Sans;
  font-style: italic;
  src: url(../public/OpenSans/OpenSans-Italic.ttf);
}

@font-face {
  font-family: Open Sans;
  font-weight: lighter;
  font-style: italic;
  src: url(../public/OpenSans/OpenSans-LightItalic.ttf);
}

@font-face {
  font-family: Open Sans;
  font-weight: bold;
  src: url(../public/OpenSans/OpenSans-Bold.ttf);
}

@font-face {
  font-family: Open Sans;
  font-weight: bold;
  font-style: italic;
  src: url(../public/OpenSans/OpenSans-BoldItalic.ttf);
}

html {
  font-family: Open Sans, sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --track-color: #6F6F6F;
  --thumb-color: #C4C4C4;
  --tag-close-color: #fd2222;
  --burger-close-btn-img: url(../public/icon-close.svg);
}

body.dark {
  --bg-color: #000000;
  --bg-color-2: #FFFFFF50;
  --bg-color-3: #00000050;
  --text-color: #FFFFFF;
  --border-color: #FFFFFF;
  --button-bg-color: #FFFFFF;
  --button-text-color: #000000;
  --burger-btn-img: url(../public/burger-icon-dark.svg);
}

body.light {
  --bg-color: #FFFFFF;
  --bg-color-2: #00000028;
  --bg-color-3: #6e6d6d73;
  --text-color: #000000;
  --border-color: #000000;
  --button-bg-color: #000000;
  --button-text-color: #FFFFFF;
  --burger-btn-img: url(../public/burger-icon-light.svg);
}

body.dark {
  background-color: var(--bg-color);
}

.btn {
  cursor: pointer;
  background-color: var(--button-bg-color);
  color: var(--button-text-color);
  border-radius: 40px;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bg {
  background-color: var(--bg-color);
}

.bg-2 {
  background-color: var(--bg-color-2);
}

.bg-3 {
  background-color: var(--bg-color-3);
}

.text {
  color: var(--text-color);
}

.bold {
  font-weight: bold;
}

* * {
  color: var(--text-color);
}

.bg-img {
  background-image: url(../public/home-bg.svg);
}

.d-flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.d-block {
  display: block;
}

.flex-d-row {
  flex-direction: row;
}

.flex-d-column {
  flex-direction: column;
}

.flex-1-1-0 {
  flex: 1 1 0;
}

.align-center {
  align-items: center;
}

.align-stretch {
  align-items: stretch;
}

.justify-stretch {
  justify-content: stretch;
}

.justify-center {
  justify-content: center;
}

.justify-end {
  justify-content: end;
}

.justify-left {
  justify-content: left;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.ml-100 {
  margin-left: 100px;
}

.clickable {
  cursor: pointer;
  user-select: none;
}

.hidden {
  display: none;
}

.visibly-hidden {
  visibility: hidden;
}

.has-top-border {
  border-top: 1px solid var(--border-color);
}

.has-y-scrollbar {
  overflow-y: scroll;
  scrollbar-color: var(--scroll-color) var(--track-color) ;
  scrollbar-width: thin;  
}

.mt-5 {
  margin-top: 5px;
}

.blinking {
  animation: blinker 1s linear 5;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}