.jokes__left-panel {
  width: 328px;
  padding: 86px 0px 0px 90px;
}

.jokes__page-title {
  font-size: 292%;
  padding-left: 40px;
}

.jokes__jokes-list {
  margin: 10px 38px 139px 0px;
  border-radius: 10px;
  font-size: 180%;
  text-align: center;
  max-height: 740px;
}

.jokes__list-item {
  cursor: pointer;
  padding: 7px 0 7px 0;
}

.jokes__list-item--active {
  background-color: var(--bg-color-3);
}

.jokes__right-panel {
  font-size: 230%;
  padding: 85px 0 88px 25px;
}

.jokes__right-panel > * {
  margin-right: 87px;
}

.jokes__right-header {
  padding: 3px 0 0px 50px
}

.jokes__joke-text {
  margin-top: 14px;
  border-radius: 10px;
  font-size: 88%;
  font-family: inherit;
  padding: 10px;
}

.jokes__joke-text--empty {
  margin: 55px;
  text-align: center;
  padding: 30%;
}

.jokes__joke-title-input {
  font-size: 88%;
  font-weight: normal;
  margin-left: 35px;
  border-radius: 10px;
  text-align: center;
  height: 26px;
  padding: 4px;
}

.jokes__prompts, .jokes__sort-tags, 
.jokes__estimated-time, .jokes__new-material {
  height: 62px;
  font-size: 85%;
  padding-left: 46px;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
}

.jokes__estimated-time small {
  font-size: 80%;
}

.jokes__estimated-time > .label {
  font-size: 80%;
  margin-left: 30px;
}

.jokes__minutes, .jokes__seconds {
  width: 80px;
}

.jokes__save-icon {
  width: 50px;
}

.jokes__search {
  border: none;
  outline: none;
  font-size: 100%;
  text-align: center;
}

.jokes__search:focus {
  border: none;
}