

.tags__tag {
  font-size: 65%;
  padding: 9px;
  margin: 4px 9px;
  flex: 1 1 0;
  display: flex;
  justify-content: space-between;
  color: var(--text-color);
  border-radius: 10px;
  text-decoration: none;
  align-items: center;
  max-width: 150px;
  max-height: 20px;
  border: none;
}

.tags__tag-close {
  cursor: pointer;
  color: var(--tag-close-color);
}