
.setlist__panel-left > .setlist__title {
  font-size: 295%;
  margin: 83px 0px 0px 378px;
}

.setlist__panel-right > .setlist__title {
  font-size: 200%;
  margin: 98px 213px 0px 5px;
}

.setlist__setlist-title-input {
  font-size: 88%;
  font-weight: normal;
  margin-left: 35px;
  border-radius: 10px;
  text-align: center;
  height: 26px;
  padding: 4px;
}

.setlist__setlist-list, .setlist__jokes-list, .setlist__list-item {
  border-radius: 10px;
}

.setlist__search {
  border: none;
  outline: none;
}

.setlist__search:focus {
  border: none;
}

.setlist__jokes-list {
  margin: 16px 87px 0px 341px;
  height: 588px;
  width: 284px;
}

.setlist__setlist-jokes-list {
  margin: 16px 215px 0px 10px;
  height: 580px;
}

.setlist__list-item {
  font-size: 184%;
  padding: 11px;
  width: 100%;
  box-sizing: border-box;
  justify-content: space-between;
}

.setlist__list-item--active, .setlist__list-item:hover {
  background-color: var(--bg-color-3);
}

.setlist__button {
  cursor: pointer;
  margin: 19px 322px 0px 68px;
  font-size: 209%;
  border-radius: 10px;
  padding: 8px;
  background-color: var(--bg-color-2);
}

.setlist__button:hover {
  background-color: var(--bg-color-3);
}

.setlist__tags-container {
  margin: 22px 86px 0 200px;
} 

.setlist__tags-container > h3 {
  font-size: 188%;
  width: 136px;  
}

.setlist__tags .tags__tag {
  font-size: 150%;
}

.setlist__new-material {
  font-size: 180%;
  margin: 10px 149px;
}

.setlist__action-button {
  font-size: 200%;
  margin: 31px 0 0 128px;
  width: 120px;
}

.setlist__time {
  margin: 15px 215px 0 18px;
}

.setlist__estimated-time {
  margin: 0 0 0 36px;
}

.setlist__input {
  font-size: 88%;
  font-weight: normal;
  margin-left: 35px;
  border-radius: 10px;
  text-align: center;
  height: 26px;
  padding: 4px;
  width: 6em;
}

.mr-215 {
  margin-right: 215px;
}